/*eslint no-unused-vars: "off"*/
/*eslint-disable @typescript-eslint/no-unused-vars */

import React from "react"
import { MyForm } from "src/core/components"
import { Button, Card, CardBody, CardHeader, CardText } from "reactstrap"
import { HLocalStorage } from "@macashipo/mlib"
import { FlowGetConfigFormFromConfigServer } from "src/helpers/Flows/Form"

const PageDev = () => {
  var configForm1 = HLocalStorage.getObj("dev_form_local", {})
  if (configForm1 == null || Object.keys(configForm1).length == 0) {
    return (
      <div>
        <Card>
          <CardHeader className="bg-light" onClick={() => {}}>
            <h5 className="mb-0">From from localstorage: dev_form_local</h5>
          </CardHeader>
          <CardBody>Missing config from localhost: dev_form_local</CardBody>
        </Card>
      </div>
    )
  }
  //check if config v1
  if (configForm1.Data && configForm1.DefaultValues) {
    configForm1 = FlowGetConfigFormFromConfigServer(configForm1, {
      defaultValues: {},
      fnList: {},
      moreDefaultValues: {},
    })
  }
  console.warn("render dev:", configForm1)
  return (
    <div>
      <Card>
        <CardHeader className="bg-light" onClick={() => {}}>
          <h5 className="mb-0">From from localstorage: dev_form_local</h5>
        </CardHeader>
        <CardBody>
          <MyForm
            configForm={configForm1}
            fnList={{
              onSubmitForm: (
                currentValues,
                { defaultValues, myForm, relativeControls } = {}
              ) => {
                console.warn("onSubmitForm currentValues:", currentValues)
                console.warn("onSubmitForm defaultValues:", defaultValues)
                console.warn("onSubmitForm:", relativeControls)
                myForm.checkValidAll()
                return new Promise((resolve, reject) => {
                  resolve("")
                })
              },
              getSourceList: () => {
                return {
                  gender: [
                    { Text: "Female", Value: "f" },
                    { Text: "Male", Value: "m" },
                  ],
                  demo: [
                    { Text: "A", Value: "a" },
                    { Text: "B", Value: "b" },
                  ],
                }
              },
            }}
          />
        </CardBody>
      </Card>
    </div>
  )
}

export default PageDev
